/*   */
.content {
    display: flex;
    gap: 40px;
    flex-wrap: wrap;
    height: auto; /*          */
  }
  
  .banner1, .banner2 {
    display: flex;
    background: #F2F4F5;
    padding: 30px;
    border-radius: 8px; /*     */
    flex: 1 1 45%; /*     45%        */
    box-sizing: border-box; /*        */
  }
  
  .banner1 {
    background: #F2F4F5;
  }
  
  .banner2 {
    background: #191C1F;
    color: white;
    position: relative;
    max-width: 600px;
    width: 100%;
  }
  
  .btn1 {
    border: 1px solid #FA8232;
    padding: 10px 30px;
    color: black; /*   */
    background-color: transparent; /*    */
    border-radius: 4px; /*   */
    cursor: pointer;
}

.btn1:hover {
    color: #FA8232; /*      */
    background-color: transparent; /*     */
    outline: 2px solid #FA8232; 
}
.btn2{
  border: 1px solid #FA8232;
    padding: 10px 30px;
    color: white; /*   */
    background-color: transparent; /*    */
    border-radius: 4px; /*   */
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease, color 0.3s ease; /*    */
}
.btn2:hover {
  color: #FA8232; /*      */
  background-color: transparent; /*     */
  outline: 2px solid #FA8232; 
}
  .paragraph, .paragraph2 {
    display: inline;
    padding: 5px 10px;
    border-radius: 4px; /*      */
  }
  
  .paragraph {
    color: white;
    background: #2DA5F3;
  }
  
  .paragraph2 {
    background: #EFD33D;
    color: black;
  }
  
  /*  */
  .banner1 img, .banner2 img {
    max-width: 100%;
    height: auto;
  }
  .price-tag {
    width: 70px;
    height: 70px;
    position: absolute;
    right: 10px;
    top: 10px;
    background: #2DA5F3;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-weight: bold;
  }
  /*     */
  @media (max-width: 750px) {
    .content {
      flex-direction: column; /*       */
      gap: 20px; /*     */
    }
  
    .banner1, .banner2 {
      flex-direction: column; /*     */
      align-items: center; /*   */
      text-align: center; /*   */
    }
  
    .banner2 img {
      position: static; /*      */
      max-width: 100%; /*        */
      height: auto;
    }
  
    .banner1 img {
      max-width: 100%; /*        */
      height: auto;
    }
  }
  .BannerImages {
    transition: transform 0.3s ease; /*    */
  }
  
  .BannerImages:hover {
    transform: translateX(20px) ; /*   skew   */
  }
  /*      */
  @media (max-width: 500px) {
    .content {
      gap: 10px; /*        */
    }
  
    .btn {
      padding: 8px 20px; /*         */
    }
    .banner2 img{
        padding-top:500pxpx;
    }

  }
  